@import url(//hello.myfonts.net/count/3983de);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#About {
    background-color: #F5EACE;
}

/* 
    The subheadings will be layed out as follows:
    
    the smallest heading will be 1.16 times the base font size
    the second smallest heading will be 1.75 times the font size
    then the main heading will be 2.67 times the font size


*/
/* reset the styles and provide defaults*/
#About .subheading h1, #About .subheading h2,
#About .subheading h3 {
    margin:0px;
    padding:0px;
}
#profile-links .profile-link {

}
#profile-links .profile-link img {
    /* 
    I will have to play with the font sizes later
    
    */
    max-height: 1em;
}

.card {
    padding: 1em;
}



/* 
    make the navbar have a variable height based on the height of the page


*/
nav#main-navigation {
    /* height: 3em; */
    /* height: 3em; */
    background: white;
    content:"Testing 123";
    display:flex;
    padding-left: 0.5em;
    padding-right: 0.5em;

}
nav#main-navigation > div#brand {
    /* display: none; */
    flex-basis: 13em;
    flex-shrink: 0.33;
    /* flex-grow: 1; */
}

nav#main-navigation > div#nav-links {
    /* font-size: 2em; */
    /* flex-basis: 75%; */
    flex-grow:3;
    display: flex;
    justify-content: space-around;
    
}

nav#main-navigation > div#brand > p.logo-text {
    margin:0px;
  
    font-weight: bolder;
    text-overflow: ellipsis;
}

nav#main div#brand img {

}


@media screen and (min-width:375px) {
    nav#main-navigation > div#nav-links a,
    nav#main-navigation > div#brand > p.logo-text {
    font-size: 1.125em;
}
@media screen and ( min-width: 425px){
    nav#main-navigation > div#nav-links a,
    nav#main-navigation > div#brand > p.logo-text {
        font-size: 1.25rem;
    }
}
@media screen and (min-width:768px) {
    nav#main-navigation > div#nav-links a,
    nav#main-navigation > div#brand > p.logo-text {
    font-size: 1.5rem;
    }
}
@media screen and (min-width:320px){
    nav#main-navigation {
        /* height: 2em; */
        /* height: 4em; */
        padding-left: 1em;
        padding-right: 1em;
    }

}

}
@media screen and (min-width:768px) {
    nav#main-navigation > div#brand{
        display: inline;
        display: initial;
    }
}
